$main-color: #ffa5ba;
$accent-color: #d41443;
$black-color: #000000;
$secondary-text-color: #907e82;
$background-color: #fff5f6;

.footer__main {
  position: relative;
  background-color: $background-color;
}

.footer__group {
  padding-top: 113px;
  padding-bottom: 25px;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 18px;
  }
}

.footer__group-title {
  width: 234px;
  @media screen and (min-width: 768px) {
    width: 340px;
  }
  @media screen and (min-width: 1200px) {
    width: 480px;
    margin-right: 257px;
  }
}

.footer__title {
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $accent-color;
  @media screen and (max-width: 767px) {
    margin-bottom: 43px;
  }
  @media screen and (min-width: 768px) {
    font-size: 48px;
    letter-spacing: 0.06em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 1.14;
  }
}

.footer__title--litle {
  font-size: 18px;
  line-height: 1.17;
  color: $main-color;
  margin-bottom: 17px;
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    margin-bottom: 16px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.13;
    margin-bottom: 15px;
  }
}

.footer__list-group {
  margin-left: auto;
  @media screen and (max-width: 767px) {
    width: 230px;
  }
  margin-bottom: 65px;
  @media screen and (min-width: 768px) {
    margin-bottom: 204px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 107px;
  }
}

.footer-list {
  display: flex;
  margin-left: auto;
  width: 152px;
  margin-bottom: 46px;
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    width: 162px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 50px;
  }
}

.footer-list__item {
  margin-right: 28px;
  color: $main-color;
  &:last-child {
    margin-right: 0;
  }
  &:hover,
  &:focus {
    fill: $accent-color;
    color: $accent-color;
  }
  @media screen and (min-width: 768px) {
    margin-right: 30px;
  }
  @media screen and (min-width: 1200px) {
    margin-right: 24px;
  }
}

.footer-list__link {
  color: currentColor;
}

.footer-list__icons {
  fill: currentColor;
  @media screen and (min-width: 768px) {
    width: 34px;
    height: 34px;
  }
  width: 32px;
  height: 32px;
}

.footer__tel-group {
  margin-left: auto;
}

.footer__text-tel {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.86;

  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}

.footer__tel {
  color: $accent-color;
}

.footer__copyright {
  font-family: DM Sans;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.52;
  margin: 0 auto;
  color: $secondary-text-color;
  width: 150px;
  @media screen and (min-width: 768px) {
    width: 278px;
    line-height: 1.86;
    letter-spacing: 0.02em;
  }

  &::before {
    content: '';
    display: block;
    position: absolute;
    bottom: 89px;
    left: 0px;
    height: 1px;
    width: 100%;

    background-color: #e1e1e1;
    border-radius: 2px;
    @media screen and (min-width: 768px) {
      bottom: 73px;
    }
  }
}
